<template>
  <div class="loader">
    <svg class="loader__spiner" :style="styles" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 100 100">
      <path d="M10 50A40 40 0 0 0 90 50A40 42 0 0 1 10 50" stroke="none" />
    </svg>

    <span v-if="label" class="loader__label">{{ label }}</span>
  </div>
</template>

<script>
export default {
  name: 'c-loader',
  props: {
    label: {
      type: String,
      default: null
    },
    size: {
      type: [String, Number],
      default: 50,
      validator: v => Number(v) > 0
    }
  },
  computed: {
    styles () {
      return {
        width: `${this.size}px`,
        height: `${this.size}px`
      }
    }
  }
}
</script>

<style scoped>
  .loader {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .loader__spiner {
    animation: spiner 1s linear infinite;
    fill: hsla(var(--ui-active), .8);
  }
  .loader__label {
    color: hsla(var(--ui-active), .5);
    font-size: 15px;
    display: block;
    margin-top: 10px;
  }

  @keyframes spiner {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
</style>
